import { Alert, HStack, Text, VStack } from "native-base";
import { Palette } from "../../theme/palette";
import { AntDesign } from "@expo/vector-icons";

const getBackgroundColorByStatus = (status = "") => {
	switch (status) {
		case "warning":
			return "#F8F4C4";
		default:
			return "red.900";
	}
};

export const CustomAlert = (props) => {
	return (
		<Alert
			w="100%"
			variant={"subtle"}
			colorScheme={props.status}
			status={props.status}
			style={{
				border: `1px solid ${Palette.warning_text}`,
			}}
			backgroundColor={getBackgroundColorByStatus(props.status)}
			rounded={"md"}
		>
			<VStack space={2} flexShrink={1} w="100%">
				<HStack
					flexShrink={1}
					space={2}
					alignItems="center"
					justifyContent="space-between"
				>
					<HStack space={4} flexShrink={1} alignItems="center">
						<AntDesign
							name="warning"
							size={22}
							color={Palette.warning_text}
						/>
						<Text color={Palette.warning_text}>
							<b style={{ fontWeight: "semibold" }}>
								Completar perfil:
							</b>{" "}
							Preencha suas informações para encontrar pacientes.
						</Text>
					</HStack>
				</HStack>
			</VStack>
		</Alert>
	);
};
